footer{
    background-image: url('../../public/images/footer_bg.png');
    background-size: cover;
}

.footer_first{
    width: 100%;
    padding: 12rem 10rem 0;
    display: flex;
}

.left_footer{
    width: 60%;
    display: flex;
    gap: 2.8rem;
}

.left_footer .products h1{
    color: #ffffff;
    font-weight: 600;
    font-size: 1.1rem;
}

.left_footer .products .left_p{
    margin-top: 0.6rem;
    color: rgb(172, 172, 172);
    font-size: 0.8rem;
}

.left_footer .products .left_p p{
    margin-top: 0.4rem;
}

/* Footer Right */
.footer_first .right_footer h1{
    color: #fff;
    font-size: 1.1rem;
    font-weight: 600;
}

.footer_first .right_footer .inputbox input{
    border: none;
    background-color: transparent;
    font-size: 1rem;
    font-weight: 600;
}
.footer_first .right_footer .inputbox button{
    background-color: #ffbe00;
    padding: 0.5rem 1rem;
    border: none;
    font-size: 0.8rem;
    font-weight: 500;
}
.footer_first .right_footer .inputbox{
    margin-top: 0.6rem;
    border: 1px solid #fff;
    display: flex;
    justify-content: space-between;
    padding: 0.5rem 1rem;
    width: 23rem;
}

.footer_first .right_footer .social_media{
    display: flex;
    margin-top: 1rem;
    gap: 2rem;
}

.footer_first .right_footer .social_media a{
    text-decoration: none;
}

.footer_first .right_footer .social_media i{
    font-size: 1.5rem;
}

.copyright{
    padding: 4rem 10rem;
    text-align: center;
}
.copyright p{
    margin-top: 1rem;
    color: #aaaaaa;
    font-size: 0.9rem;
}

@media (max-width: 640px){
    footer{
        background-size: cover;
    }

    .footer_first{
        width: 100%;
        padding: 16rem 1rem 0;
        display: flex;
        flex-direction: column;
    }

    .footer_first .left_footer{
        width: 100%;
        display: grid;
        grid-template-columns: 1fr 1fr;
        row-gap: 1rem;
    }
    .footer_first .left_footer .products h1{
        font-size: 0.9rem;
    }

    .footer_first .left_footer .products .left_p p{
        font-size: 0.6rem;
    }

    .footer_first .right_footer{
        margin-top: 2rem;
        width: 100%;
    }

    .footer_first .right_footer h1{
        font-size: 1rem;
    }

    .footer_first .right_footer .inputbox{
        width: 100%;
    }

    .footer_first .right_footer .inputbox input{
        font-size: 0.9rem;
    }

    .footer_first .right_footer .social_media i{
        font-size: 1rem;
    }

    .copyright p{
        font-size: 0.8rem;
    }
    .copyright{
        width: 100%;
        padding: 2rem 1rem;
        text-align: center;
    }
}