.header{
    width: 100%;
}

.navbar{
    width: 100%; 
    padding: 1.5rem 10rem;
    display: flex;
    justify-content: space-between;
    background-color: #fff;
    box-shadow: 0 0 10px rgb(156, 156, 156);
}

.ham_menu{
    display: none;
}

.nav_logo{
    display: flex;
    align-items: center;
    gap: 3rem;
}

.search_box{
    width: 40rem;
    border-radius: 0.4rem;
    background-color: #d8d8d8;
}

.search_box .inputbox{
    display: flex;
    align-items: center;
    padding: 0.625rem 1.25rem;
    gap: 0.5rem;
}

.search_box .inputbox input{
    flex: 1;
    height: 1.563rem;
    background: transparent;
    border: 0;
    outline: 0;
    font-size: 1rem;
    color: #333;
}

.inputbox .navbar-icon-search{
    font-size: 1.1rem;
}
.inputbox button{
    background: transparent;
    border: 0;
    outline: 0;
}

.navbar .second_box{
    display: flex;
    align-items: center;
    gap: 2rem;
}

.log_sign{
    display: flex;
    gap: 1rem;
}

.log_sign button{
    background-color: #ffbe00;
    padding: 0.7rem 1rem;
    border-radius: 0.4rem;
    border: none;
    font-weight: 600;
    font-size: 1rem;
    cursor: pointer;
}

.log_btns{
    text-decoration: none;
    color: #000;
}

.navbar-link{
    color: black;
}
.cart{
    position: relative;
}
.total_quantity{
    position: absolute;
    height: 1.2rem;
    width: 1.2rem;
    right: -9px;
    bottom: -4px;
    background-color: #ffbe00;
    border-radius: 50%;
    font-size: 14px;
    text-align: center;
}

.navbar-icon{
    font-size: 2.3rem;
}

.phone_menu_sec{
    display: none;
}

@media (max-width: 640px) {
    .search_box, .log_sign{
        display: none;
    }

    .navbar{
        width: 100%;
        padding: 1rem;
        align-items: center;
    }

    .ham_menu{
        display: flex;
    }

    .ham_menu_icon{
        font-size: 2rem
    }

    .phone_menu{
        background-color: rgba(0, 0, 0, 0.325);
        width: 100vw;
        height: 100vh;
        position: fixed;
        z-index: 1000;
        left: 0;
        top: 0;
        display: inline-block;
    }

    .phone_menu_close{
        display: none;
    }

    .nav-close{
        color: #ffffff;
        font-size: 2rem;
    }

    .close_phone_nav{
        background-color: transparent;
        border: none;
    }

    .phone_menu .phone_menu_sec{
        background-color: #ffffff;
        width: 80vw;
        height: 100vh;
        border-radius: 0 2rem 0 0;
        overflow: hidden;
        position: relative;
        display: inline-block;
    }

    .menu_first_page{
        width: 100%;
        height: 28vh;
        padding: 2rem;
        display: flex;
        align-items: end;
        background-color: #262626;
    }

    .profile_details{
        display: flex;
        flex-direction: column;
        gap: 0.5rem;
    }

    .profile_details h1{
        font-size: 1.2rem;
        color: #ffffff;
        font-weight: 500;
    }

    .profile_details span{
        font-weight: 700;
    }

    .profile_details_btn{
        display: flex;
        gap: 0.5rem;
    }

    .profile_details button{
        background-color: #ffbe00;
        font-size: 0.9rem;
        font-weight: 600;
        padding: 0.5rem 1rem;
        border: none;
        border-radius: 0.4rem;
    }

    .close_phone_nav{
        position: absolute;
        top: 2rem;
        right: 1.5rem;
    } 

    .search-box-mobile{
        padding: 1rem;
        display: flex;
        justify-content: center;
    }

    .inputbox-mobile{
        width: 100%;
        display: flex;
        align-items: center;
        padding: 0.625rem 1.25rem;
        gap: 0.5rem;
        border-radius: 0.4rem;
        background-color: #d8d8d8;
    }

    .inputbox-mobile button{
        border: none;
        background-color: transparent;
    }

    .navbar-icon-search{
        font-size: 1.1rem;
    }

    .inputbox-mobile input{
        flex: 1;
        height: 1.563rem;
        background: transparent;
        border: 0;
        outline: 0;
        font-size: 1rem;
        color: #333;
    }

    .menu_sec_page{
        padding: 1rem;
        display: flex;
        flex-direction: column;
        gap: 2rem;
    }

    .phn_categ{
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;
    }

    .phn_categ .phn_categ_heading{
        background-color: #ffbe00;
        width: 5px;
        padding: 0.3rem 0;
        white-space: nowrap;
    }
    
    .phn_categ .phn_categ_heading h1{
        font-size: 1rem;
        text-transform: capitalize;
        margin-left: 0.8rem;
    }

    .phn_categ .prod_categ{
        font-weight: 500;
        font-size: 0.8rem;
    }

    .phone_menu_sec .phn_next_categ{
        display: flex;
        flex-wrap: wrap;
        gap: 0.5rem;
        justify-content: space-evenly;
    }

    .phn_next_categ button{
        background-color: #f8cc46;
        width: 7rem;
        height: 3.5rem;
        border: transparent;
        border-radius: 0.4rem;
        padding: 0.4rem;
    }

    .phn_next_categ .menu-btn{
        text-decoration: none;
        text-transform: uppercase;
        font-weight: 600;
        color: #000;
        font-size: 0.8rem;
    }

    .navbar .nav_logo{
        margin-left: -10rem;
    }

    .navbar .nav_logo svg{
        height: 1.5rem;
    }
}